import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Injectable } from "@angular/core";
import { Observable, map } from "rxjs";
import { environment } from "src/environments/environment.prod";

@Injectable({
  providedIn: "root",
})
export class RequisitionService {
  constructor(private http: HttpClient) {}

  // http://localhost:9090/api/v1/purchaseRequisition/view

  getRequisitions(): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/view`;
    return this.http.get<any[]>(RequisitionssUrl);
  }

  getRequisitionsByStatus(params: any): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/find/all/by/approval/status`;
    return this.http.get<any[]>(RequisitionssUrl, {
      params: params,
    });
  }

  getRequisitionsById(params: any): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/find/by/id`;

    return this.http.get<any[]>(RequisitionssUrl, {
      params: params,
    });
  }

  getRequisitionsByCode(params: any): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/find/by/code`;

    return this.http.get<any[]>(RequisitionssUrl, {
      params: params,
    });
  }

  addRequisitions(data: any): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/add`;
    return this.http.post<any>(RequisitionssUrl, data);
  }

  updateRequisitions(RequisitionsDetails: any): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/update`;
    return this.http.put<any>(updateBillUrl, RequisitionsDetails);
  }

  deleteRequisitionsTemporarily(params: any): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/delete`;
    return this.http.delete<any[]>(RequisitionssUrl, {
      params: params,
    });
  }

  validateRequisition(params: any): Observable<any> {
    const API_URL = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/approveOrRejectOrReturn`;
    return this.http.put(
      API_URL,
      {},
      {
        params: params,
      }
    );
  }

  // ********************************************************************************************************************

  sendPurchaseOrder(params: any): Observable<any> {
    const RequisitionssUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/send/po`;
    return this.http.post<any>(RequisitionssUrl, {}, { params });
  }

  donwloadPurchaseOrder(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/download/po`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download PO", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }
  //*********************************************************************************************************************** */

  receivePurchaseOrderItems(data: any): Observable<any> {
    const ReceiveUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/item/receive`;
    return this.http.post<any>(ReceiveUrl, data);
  }
  getReceivedPurchaseOrderItems(params: any): Observable<any> {
    const ReceiveUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/find/delivery/by/prCode`;

    return this.http.get<any[]>(ReceiveUrl, {
      params: params,
    });
  }

  getReceivedItemsByStatus(params: any): Observable<any> {
    const ReceiveUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/find/delivery/by/pr_category`;
    return this.http.get<any[]>(ReceiveUrl, {
      params: params,
    });
  }

  deleteReceivedItemsTemporarily(params: any): Observable<any> {
    const ReceiveUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/delete`;
    return this.http.delete<any[]>(ReceiveUrl, {
      params: params,
    });
  }

  validateReceivedItemsAllocation(data: any, params: any): Observable<any> {
    const ReceiveUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/post/delivery/to/allocations`;

    return this.http.put<any>(ReceiveUrl, data, { params });
  }

  // donwloadGoodsReceivedNote(params: any): Observable<any> {
  //   let headers = new HttpHeaders();
  //   headers.append("Accept", "application/pdf");
  //   let requestOptions: any = {
  //     params: params,
  //     headers: headers,
  //     responseType: "blob",
  //     withCredentials: false,
  //   };
  //   let API_URL = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/item/receive/download/receiptnote`;
  //   return this.http.get(API_URL, requestOptions).pipe(
  //     map((response: BlobPart) => {
  //       console.log("Hey this is download GRN", response);
  //       return {
  //         filename: "Report",
  //         data: new Blob([response], { type: "application/pdf" }),
  //       };
  //     })
  //   );
  // }


  donwloadSingleGoodsReceivedNote(params: any): Observable<any> {
    let headers = new HttpHeaders();
    headers.append("Accept", "application/pdf");
    let requestOptions: any = {
      params: params,
      headers: headers,
      responseType: "blob",
      withCredentials: false,
    };
    let API_URL = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/item/receive/download/receiptnote/single`;
    return this.http.get(API_URL, requestOptions).pipe(
      map((response: BlobPart) => {
        console.log("Hey this is download GRN", response);
        return {
          filename: "Report",
          data: new Blob([response], { type: "application/pdf" }),
        };
      })
    );
  }
  updateReceivedItemsTransactionStatus(params): Observable<any> {
    const updateBillUrl = `${environment.baseUrlProcurement}/api/v1/purchaseRequisition/update/record/on/transaction`;
    return this.http.put<any>(updateBillUrl, {}, {params});
  }
  
}
