import { Component, Inject, OnInit } from '@angular/core';
import { ManageProcurementCommitteesComponent } from '../manage-procurement-committees/manage-procurement-committees.component';

import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { Subscription } from 'rxjs';
import { SnackbarService } from 'src/app/shared/services/snackbar.service';
import { ProcurementCommitteeService } from 'src/app/erp-procurement/data/services/procurement-committee.service';

@Component({
  selector: 'app-delete-committee-details',
  templateUrl: './delete-committee-details.component.html',
  styleUrls: ['./delete-committee-details.component.sass']
})
export class DeleteCommitteeDetailsComponent implements OnInit {

  constructor(public dialogRef: MatDialogRef<ManageProcurementCommitteesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
    private snackbar: SnackbarService,
    private service: ProcurementCommitteeService) { }

  committee: any;
  subscription!: Subscription;
  loading = false;

  ngOnInit(): void {
    this.committee = this.data.data.committeeName;
  }

  onDelete() {
    this.loading = true;
    this.subscription = this.service.deleteCommittee(this.data.data.id).subscribe(res => {
      this.loading = false;
      this.snackbar.showNotification("snackbar-success", "Successful!");
      this.dialogRef.close();
    }, err => {
      this.loading = false;
      this.snackbar.showNotification("snackbar-danger", err);
      this.dialogRef.close();
    })
  }

  onClick() {
    this.dialogRef.close();
  }

}
