import { SelectionModel } from '@angular/cdk/collections';
import { HttpParams } from '@angular/common/http';
import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatMenuTrigger } from '@angular/material/menu';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';
import { Subject, takeUntil, Subscription } from 'rxjs';
import { RequisitionService } from 'src/app/erp-procurement/data/services/requisition.service';
import { ManageRfqsComponent } from 'src/app/erp-procurement/modules/rfqs/pages/manage-rfqs/manage-rfqs.component';

import { SnackbarService } from 'src/app/shared/services/snackbar.service';
 
@Component({
  selector: 'app-requisition-lookup',
  templateUrl: './requisitions-lookup.component.html',
  styleUrls: ['./requisitions-lookup.component.sass']
})

export class RequisitionsLookupComponent implements OnInit {

  displayedColumns: string[] = [
    "select",
    "id",
    'requisitionCode', 'operatingUnit', 'requester', 'createdDate', 'procurementApproval'];

  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  contextMenu: MatMenuTrigger;

  selection = new SelectionModel<any>(false, []);

  requsitionDetails: any;
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;

  dataSourceFilteredList: any[] = [];
  requsitionsArray: any[] = [];

    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;

  isLoading: boolean = true;

  Form: FormGroup;
  currentDate: Date = new Date();
  dateTomorrow: Date = new Date(
    this.currentDate.getTime() + 1 * 24 * 60 * 60 * 1000
  );
  oneWeekAgo: Date = new Date(
    this.currentDate.getTime() - 7 * 24 * 60 * 60 * 1000
  );

  constructor(
    public dialogRef: MatDialogRef<ManageRfqsComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private fb: FormBuilder,
    private snackbar: SnackbarService,
    private requisitionService: RequisitionService
  ) { }

  ngOnInit(): void {
    this.Form = this.fb.group({
      hodStatus: ["APPROVED", Validators.required],
      financeStatus: ["APPROVED", Validators.required],
      procurementStatus: ["APPROVED", Validators.required],
      fromDate: [this.oneWeekAgo, Validators.required],
      toDate: [this.dateTomorrow, Validators.required],
    });

    this.getData();

  }
  ngAfterViewInit() {

    console.log("Finally: ", this.dataSourceFilteredList);


  }


  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }

  filter() {
    //let storeId = [1, 2, 3];
    this.dataSource.data.forEach((element) => {
      this.requsitionsArray.forEach((item) => {
        if (item === element.id) {
          // this.dataSourceFilteredList.push(this.dataSource.data.indexOf(element));
          this.selection.select(element);
        }
      });
    });

    console.log("dataSourceFilteredList ", this.dataSourceFilteredList);
  }


  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  //******************************************************************************************************
  //Select expense

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${row.position + 1
      }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }
  proceed() {
    this.dialogRef.close({ event: "close", data: this.selectedRows });

    //   console.log(data);
  }

  onNoClick(): void {
    this.dialogRef.close({ event: "close", data: this.selectedRows });
  }
  public confirmAdd(): void { }




  // getData() {
  //   this.vendorService.fetchAllVendors().pipe(takeUntil(this.destroy$)).subscribe(
  //     {
  //       next: (res) => {
  //         this.data = res;



  //         this.requsitionDetails = res;
  //         this.requsitionsArray = this.data.selected;

  //         console.log("this.requsitions: ", this.requsitionDetails);

  //         this.dataSource = new MatTableDataSource<any>(this.requsitionDetails);
  //         this.dataSource.paginator = this.paginator;
  //         this.dataSource.sort = this.sort;

  //         // if (this.requsitionsArray && this.requsitionsArray.length !== 0) {
  //         //   this.filter();
  //         //   console.log("filter by: ", this.requsitionsArray);
  //         // }

  //         this.isLoading = false;
  //       },
  //       error: (err) => {
  //         this.snackbar.showNotification(
  //           "snackbar-danger", err
  //         );
  //       },
  //       complete: () => {

  //       }

  //     }
  //   ), Subscription;
  // }

  getData() {

    const params = new HttpParams()

      .set("financeApprovalStatus", this.Form.value.financeStatus)
      .set("procurementApprovalStatus", this.Form.value.procurementStatus)
      .set("hodApprovalStatus", this.Form.value.hodStatus)
      .set("fromDate", this.Form.value.fromDate)
      .set("toDate", this.Form.value.toDate)


    this.requisitionService.getRequisitionsByStatus(params).pipe(takeUntil(this.destroy$)).subscribe(
      {
        next: (res) => {
          if (res) {
            this.data = res;
            this.isLoading = false;
            this.dataSource = new MatTableDataSource(this.data);
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;

            console.log("this.data :", this.data)

          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
              res
            )
          }
        },
        error: (err) => {
          this.snackbar.showNotification(
            "snackbar-danger",
            "No Requisitions Matching these parameters!"
          );
        },
        complete: () => {

        }

      }
    ), Subscription;



  }


 

}
