import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { ManageProcurementCommitteesComponent } from '../manage-procurement-committees/manage-procurement-committees.component';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatTableDataSource } from '@angular/material/table';

@Component({
  selector: 'app-view-committee-details',
  templateUrl: './view-committee-details.component.html',
  styleUrls: ['./view-committee-details.component.sass']
})
export class ViewCommitteeDetailsComponent implements OnInit {
  loading = false;
  displayedColumns: string[] = [
    "id",
    "memberName",
    "memberPosition",
  ];

  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  constructor(
    public dialogRef: MatDialogRef<ManageProcurementCommitteesComponent>,
    @Inject(MAT_DIALOG_DATA) public data: any,
  ) {
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  ngOnInit(): void {
    //Display in a form
    this.dataSource = new MatTableDataSource<any>(this.data.dt.committeeMembers);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
  }

  onCancel() {
    this.dialogRef.close();
  }
}
