import { Component, Inject, OnInit } from '@angular/core';
import { ManageProcurementCommitteesComponent } from '../manage-procurement-committees/manage-procurement-committees.component';
import { SelectionModel } from '@angular/cdk/collections';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { Subscription } from 'rxjs';

@Component({
  selector: 'app-manage-committee-members',
  templateUrl: './manage-committee-members.component.html',
  styleUrls: ['./manage-committee-members.component.sass']
})
export class AddCommitteeMembersComponent implements OnInit {

  selection = new SelectionModel<any>(true, []);

  loading = false;
  addTeamForm: FormGroup;
  members: any;

  constructor(public dialogRef: MatDialogRef<ManageProcurementCommitteesComponent>,
    private fb: FormBuilder, @Inject(MAT_DIALOG_DATA) public data: any,) { }
  subscription!: Subscription;

  pageAction = 'Add';

  ngOnInit(): void {
    this.members = this.data.data
    this.pageAction = this.data.action

    if (this.pageAction === 'Add') {
      this.addTeamForm = this.fb.group({
        memberFullName: ["", [Validators.required]],
        memberPosition: ["", [Validators.required]],
        email: ["", [Validators.required,Validators.email]],
        phone: ["", [Validators.required]],
      })

    } else if (this.pageAction === 'Update') {
      this.addTeamForm = this.fb.group({
        id: [this.members.id],
        memberFullName: [this.members.memberFullName, [Validators.required]],
        memberPosition: [this.members.memberPosition, [Validators.required]],
        email: [this.members.email, [Validators.required,Validators.email]],
        phone: [this.members.phone, [Validators.required]],
      })

    }

  }


  onSubmit() {
    // let m: Members = new Members();
    // m.memberFullName = this.addTeamForm.value.memberFullName;
    // m.memberPosition = this.addTeamForm.value.memberPosition;
    // m.email = this.addTeamForm.value.email;
    // m.phone = this.addTeamForm.value.phone;
    // this.members.push(m);
    console.log("this.addTeamForm.value: ", this.addTeamForm.value);

    this.dialogRef.close({ event: "close", members: this.addTeamForm.value });
  }
}

