import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { RoutePrivilegeGuard } from '../../data/services/_AccessControlAuthGuard.service';

import { AllProcurementCommitteesComponent } from './pages/procurement-committee/all-procurement-committees/all-procurement-committees.component';
import { ManageProcurementCommitteesComponent } from './pages/procurement-committee/manage-procurement-committees/manage-procurement-committees.component';


const routes: Routes = [

  //Procurement Commttees
  {
    path: "all-procurement-committees",
    component: AllProcurementCommitteesComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["All Committees"] },
  },
  {
    path: "manage-procurement-committees",
    component: ManageProcurementCommitteesComponent,
    canActivate: [RoutePrivilegeGuard],
    data: {  clientName: 'ProcurementModule', requiredPrivilege:["Manage Committee"] },
  },

  


];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ParametersRoutingModule { }
