import { SelectionModel } from "@angular/cdk/collections";
import { Component, OnInit, ViewChild } from "@angular/core";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";
import { MatMenuTrigger } from "@angular/material/menu";
import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { Router } from "@angular/router";
import { Subscription, Subject, takeUntil } from "rxjs";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { DeleteCommitteeDetailsComponent } from "../delete-committee-details/delete-committee-details.component";
import { ViewCommitteeDetailsComponent } from "../view-committee-details/view-committee-details.component";
import { ProcurementCommitteeService } from "src/app/erp-procurement/data/services/procurement-committee.service";

import Swal from "sweetalert2";
import { HttpParams } from "@angular/common/http";


@Component({
  selector: "app-all-procurement-committees",
  templateUrl: "./all-procurement-committees.component.html",
  styleUrls: ["./all-procurement-committees.component.sass"],
})
export class AllProcurementCommitteesComponent implements OnInit {
  displayedColumns: string[] = [
    "select",
    "id",
    "committeeName",
    "committeeAbbr",
    "committeeDesc",
    "postedBy",
    "postedTime",
    "status",
    "action",
  ];
  dataSource!: MatTableDataSource<any>;
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;
  contextMenu: MatMenuTrigger;
  contextMenuPosition = { x: "0px", y: "0px" };
  subscription!: Subscription;
  selection = new SelectionModel<any>(true, []);
  data: any;
  error: any;
  formData: any;
  isLoading = true;
  pageFunction = "Update";
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;


  constructor(
    private router: Router,
    private dialog: MatDialog,
    private snackbar: SnackbarService,
    private committeeService: ProcurementCommitteeService
  ) {}

  ngOnInit(): void {
    this.getData();
  }
  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  editDetails(data: any) {
    const serializedData = JSON.stringify(data);
    let route = "/erp-procurement/parameters/manage-procurement-committees";
    this.router.navigate([route], {
      queryParams: { id: data.id, data: serializedData, action: "Update" },
    });
  }

  
  deleteCommitttee(id: number) {
    Swal.fire({
      title: "Are you sure?",
      text: "Do you really want to delete this Committtee?",
      icon: "question",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, Delete Committtee!",
    }).then((result) => {
      if (result.isConfirmed) {
        const params = new HttpParams().set("id", id);
        this.committeeService
          .deleteCommittee(params)
          .pipe(takeUntil(this.destroy$))
          .subscribe({
            next: (res) => {
              this.snackbar.showNotification(
                "snackbar-success",
                res.message
              );
            },
            error: (err) => {
              this.snackbar.showNotification(
                "snackbar-danger",
                err.message
              );
            },
            complete: () => {
              this.getData();
            },
          }),
          Subscription;
      }
    });
  }

  viewDetailsCall(data) {
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "50%";
    dialogConfig.data = {
      dt: data,
    };
    this.dialog.open(ViewCommitteeDetailsComponent, dialogConfig);
  }

  getData() {
    this.committeeService
      .fetchAllCommittees()
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res: ", res);
          this.data = res;
          this.isLoading = false;
          this.dataSource = new MatTableDataSource(this.data.entity);
          this.dataSource.paginator = this.paginator;
          this.dataSource.sort = this.sort;
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err);
        },
        complete: () => {},
      }),
      Subscription;
  }

  refresh() {
    this.getData();
  }

  addCommittee() {
    this.router.navigate([
      "/erp-procurement/parameters/manage-procurement-committees",
    ]);
  }

  //******************************************************************************************************
  //Select Committees
  CommitteeDetails: any;
  selectedRows: any[] = [];
  atleastOneSelected: boolean = false;

  /** Whether the number of selected elements matches the total number of rows. */
  isAllSelected() {
    const numSelected = this.selection.selected.length;
    const numRows = this.dataSource.data.length;
    return numSelected === numRows;
  }

  /** Selects all rows if they are not all selected; otherwise clear selection. */
  masterToggle() {
    if (this.isAllSelected()) {
      this.selection.clear();
      return;
    }

    this.selection.select(...this.dataSource.data);
  }

  /** The label for the checkbox on the passed row */
  checkboxLabel(row?: any): string {
    if (!row) {
      return `${this.isAllSelected() ? "deselect" : "select"} all`;
    }
    return `${this.selection.isSelected(row) ? "deselect" : "select"} row ${
      row.position + 1
    }`;
  }

  expSelected() {
    this.atleastOneSelected = true;
    this.selectedRows = this.selection.selected;

    console.log("this.selectedRows: ", this.selectedRows);
  }

  processMultipleRows(status: any) {
    const processedRows = this.selectedRows.map((element) => {
      return {
        id: element.id,
        status: status,
      };
    });

    console.log("LOG: ", processedRows);

    this.committeeService
      .validateCommittees(processedRows)
      .pipe(takeUntil(this.destroy$))
      .subscribe({
        next: (res) => {
          console.log("res: ", res);
          if (res.statusCode == 200) {
            this.snackbar.showNotification(
              "snackbar-success",
              res.message
            );

            this.selection = new SelectionModel<any>(true, []);
          } else {
            this.snackbar.showNotification(
              "snackbar-danger",
             res.message
            );
          }
        },
        error: (err) => {
          this.snackbar.showNotification("snackbar-danger", err.message);
        },
        complete: () => {
          this.getData();
        },
      }),
      Subscription;
  }
  //*************************************************************************************************************** */
}
