import { Component, OnInit, ViewChild } from "@angular/core";
import { FormGroup, FormBuilder, Validators, FormArray } from "@angular/forms";
import { ActivatedRoute, Router } from "@angular/router";
import { BehaviorSubject, Subject, takeUntil, Subscription } from "rxjs";
import { TokenStorageService } from "src/app/core/service/token-storage.service";
import { SnackbarService } from "src/app/shared/services/snackbar.service";
import { AddCommitteeMembersComponent } from "../manage-committee-members/manage-committee-members.component";
import { MatDialog, MatDialogConfig } from "@angular/material/dialog";

import { MatPaginator } from "@angular/material/paginator";
import { MatSort } from "@angular/material/sort";
import { MatTableDataSource } from "@angular/material/table";
import { ProcurementCommitteeService } from "src/app/erp-procurement/data/services/procurement-committee.service";

@Component({
  selector: "app-manage-procurement-committees",
  templateUrl: "./manage-procurement-committees.component.html",
  styleUrls: ["./manage-procurement-committees.component.sass"],
})
export class ManageProcurementCommitteesComponent implements OnInit {
  showForm = true;
  pageFunction = "Add";
  mngForm: FormGroup;
  currentUser: any;
  committeeMembers: any[] = [];

  displayedColumns: string[] = [
    "id",
    "memberFullName",
    "memberPosition",
    "email",
    "phone",
    "actions",
  ];

  dataSource = new MatTableDataSource<any>([]);
  @ViewChild(MatPaginator) paginator!: MatPaginator;
  @ViewChild(MatSort) sort!: MatSort;

  formData: any;
  public isFileLoading = new BehaviorSubject(false);
    destroy$: Subject<boolean> = new Subject<boolean>();

  downloadLoading: boolean = false;
  constructor(
    private fb: FormBuilder,
    private dialog: MatDialog,
    private tokenStorageService: TokenStorageService,
    private committeeService: ProcurementCommitteeService,
    private snackbar: SnackbarService,
    private route: ActivatedRoute,
    private router: Router
  ) {
    this.currentUser = this.tokenStorageService.getUser().username;
  }

  ngOnDestroy(): void {
    this.destroy$.next(true);
    this.destroy$.complete();
  }
  ngOnInit(): void {
    this.getPage();
    this.showForm == true;

    this.route.queryParams.subscribe((params) => {
      if (params.id) {
        const action = params.action;

        if (action === "Update") {
          this.formData = JSON.parse(params.data);
          this.pageFunction = "Update";
          if (this.formData.committeeMembers.length > 0) {
            //dispay table
            this.committeeMembers = this.formData.committeeMembers;
            this.dataSource = new MatTableDataSource<any>(
              this.committeeMembers
            );
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          }
        } else if (action === "View") {
          this.formData = JSON.parse(params.data);
          this.pageFunction = "View";
          if (this.formData.committeeMembers.length > 0) {
            //dispay table
            this.committeeMembers = this.formData.committeeMembers;
            this.dataSource = new MatTableDataSource<any>(
              this.committeeMembers
            );
            this.dataSource.paginator = this.paginator;
            this.dataSource.sort = this.sort;
          }
        }
      }

      this.getPage();
    });
  }
  ngAfterViewInit() {
    if (
      this.formData.committeeMembers &&
      this.formData.committeeMembers.length > 0
    ) {
      //dispay table
      this.committeeMembers = this.formData.committeeMembers;
      this.dataSource = new MatTableDataSource<any>(this.committeeMembers);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
    }
  }

  applyFilter(event: Event) {
    const filterValue = (event.target as HTMLInputElement).value;
    this.dataSource.filter = filterValue.trim().toLowerCase();
    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }

  manageTeamMembers(row, action) {
   
    const dialogConfig = new MatDialogConfig();
    dialogConfig.disableClose = false;
    dialogConfig.autoFocus = true;
    dialogConfig.width = "35%";
    dialogConfig.data = {
      data: row,
      action: action,
    };
    const dialogRef = this.dialog.open(
      AddCommitteeMembersComponent,
      dialogConfig
    );
    dialogRef.afterClosed().subscribe((result) => {
      if (result) {
        if (action === 'Add') {
          this.committeeMembers.push(result.members);
        } else if (action === 'Update') {
          const indexToUpdate = this.committeeMembers.indexOf(row);
          if (indexToUpdate !== -1) {
            this.committeeMembers.splice(indexToUpdate, 1, result.members);
          } else {
            console.log("Member not found!");
          }
        }
        
        
      
        //this.committeeMembers = result.members;
        console.log(" this.committeeMembers: ", this.committeeMembers);

        this.mngForm.patchValue({
          committeeMembers: this.committeeMembers,
          numberOfMembers: this.committeeMembers.length,
          createdBy: this.currentUser,
        });

        //Display in a form
        this.dataSource = new MatTableDataSource<any>(this.committeeMembers);
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
      }
    });
  }

  //Remove Committee Member
  removeMember(index: any) {
    this.committeeMembers.splice(index, 1);
    this.dataSource = new MatTableDataSource<any>(this.committeeMembers);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.mngForm.patchValue({
      committeeMembers: this.committeeMembers,
    });
  }

  getPage() {
    if (this.pageFunction == "Add") {
      this.generateEmptyForm();
      this.showForm = true;
    } else if (this.pageFunction == "Update") {
      this.generateFormWithData();
      this.showForm = true;
    }
  }
  generateEmptyForm() {
    this.mngForm = this.fb.group({
      id: [""],
      committeeAbbr: ["", [Validators.required]],
      committeeName: ["", [Validators.required]],
      committeeDesc: ["", [Validators.required]],
      numberOfMembers: [0],
      createdBy: [""],
      committeeMembers: [[]],
    });
  }

  generateFormWithData() {
    this.mngForm = this.fb.group({
      id: [this.formData.id],
      committeeAbbr: [this.formData.committeeAbbr, [Validators.required]],
      committeeName: [this.formData.committeeName, [Validators.required]],
      committeeDesc: [this.formData.committeeDesc, [Validators.required]],
      numberOfMembers: [this.formData.numberOfMembers],
      committeeMembers: [this.formData.committeeMembers],
      createdBy: [this.formData.createdBy],
    });
  }

  cancel() {
    this.router.navigate([
      "/erp-procurement/parameters/all-procurement-committees",
    ]);
  }

  onSubmit() {
    if (this.pageFunction == "Add") {
      this.committeeService
        .addNewCommittee(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);
            if (res.statusCode == 201) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate([
                "/erp-procurement/parameters/all-procurement-committees",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification("snackbar-danger", err.message);
          },
          complete: () => {},
        }),
        Subscription;
    } else if (this.pageFunction == "Update") {
      console.log("this.mngForm.value: ", this.mngForm.value);
      this.committeeService
        .updateCommittee(this.mngForm.value)
        .pipe(takeUntil(this.destroy$))
        .subscribe({
          next: (res) => {
            console.log("res: ", res);
            if (res.statusCode == 200) {
              this.snackbar.showNotification("snackbar-success", res.message);
              this.router.navigate([
                "/erp-procurement/parameters/all-procurement-committees",
              ]);
            } else {
              this.snackbar.showNotification("snackbar-danger", res.message);
            }
          },
          error: (err) => {
            this.snackbar.showNotification(
              "snackbar-danger",
              err.message
            );
          },
          complete: () => {},
        }),
        Subscription;
    }
  }
}
